@import url('https://fonts.googleapis.com/css2?family=Ceviche+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dongle&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

marquee{
    background-color: rgb(255,153,0);
    color: white;
}

.brand_name{
    font-family: 'Ceviche One', cursive;
    font-size: 3.6rem;  
}

span{
    font-size: 2rem;
}

.brand_list{
    padding: 0px 20px !important;
    color: black !important;
    font-family: 'Dongle', sans-serif;
    font-size: 1.7rem;
    transition: 0.8s;
}

.brand_list:hover{
    color: brown !important;
    transform: translateY(-5px);
}

/* ---------------------------------------------------------- */

.Sliders{
    width: 100%;    
}

/* ---------------------------------------------------------- */

.brand_review p{
    text-align: justify;
}

.brand_row{
    margin: 50px 20px;
}

.brand_para{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    word-spacing: 5px;
    line-height: 2;
    font-size: 20px;
}

.brand_forms{
    margin: 0px 30px;
}

.brand_forms h3{
    color: brown;
}

/* ---------------------------------------------------------- */

.brand_images{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
    grid-gap:30px;
    width:80%;
    margin: 50px auto 50px;
}

.brand_images_img{
    width: 100%;
    height: 100%;
}

.images h1{
    font-size: 3.5rem;
    margin: 100px 0;
}

/* ---------------------------------------------------------- */

.brand_our_products{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
    grid-gap:30px;
    width:80%;
    margin: 50px auto 50px;
}

.brand_products{
    width: 100%;
    opacity: 0.8;
}

.brannd_split{
    position: relative;
}

.overlay{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    background: linear-gradient(to top, black, #00d4ff, black);
    transition: 3s;
}

.content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: 	#2f4f4f;
    text-align: center;
    font-family: 'Ceviche One', cursive;
}

.content h3{
    font-size: 50px;
}

.brannd_split:hover .overlay{
    opacity: 0.7;
}


.our_products h1{
    font-size: 3.5rem;
    margin: 70px 0;
}

/* ---------------------------------------------------------- */

.pruchase_list{
    margin-top: 150px;
    padding: 0 15px;
}

.lists{
    margin: 5px 0;
}

/* ---------------------------------------------------------- */

.footers{
    color: white;
    padding: 20px;
    margin-top: 150px;
    background-color: #2f4f4f;
}

.social_media a{
    text-decoration: none;
    margin-right: 15px;
    color: white;
    border-bottom:2px solid white;
}

.contact a{
    text-decoration: none;
    margin-right: 15px;
    color: white;
}

/* ---------------------------------------------------------- */